import React from 'react'; import styled from 'styled-components'; 
// -------------------------------------------------------
import ImageGallery from 'react-image-gallery';
import images  from '~Client/Containers/Content/AboutMe/Components/Music/Photo';
const Music = () => (
    <Wrapper>
        <div style={{ background: 'rgba(26,0,13,.9)'}}> 
            <Header>Музыка</Header>
        </div>
        <ImageGallery styles={{height: '1000px'}} items={images}></ImageGallery>
        <TextWR>
            <Paragraph>Я очень люблю музыку, когда-то сильно хотел выступать</Paragraph>
            <Paragraph>Любили с друзьями тяжелый роЦкк :0 , и обычное слушание дошло до выступлений</Paragraph>
            <Paragraph>Вдруг для меня появилась новая мечта, стать роцК звездой</Paragraph>
            <Paragraph>Но после парочки выступлений группа распалась, а найти новую не увенчалось успехом</Paragraph>
            <Paragraph>В моем городе Запорожье, найти человека который действительно умел бы играть редкость...</Paragraph>
            <Paragraph>Затем я переехал жить в Москву, и там у меня появились некоторые проблемы, которые оторвали меня от этой связи</Paragraph>
            <Paragraph>Внутренее желание увенчалось</Paragraph>
            <Paragraph>Но я надеюсь что смогу заработать денег, чтоб сделать студию для себя, где я бы играл на инструментах :)</Paragraph>
        </TextWR>
    </Wrapper>
)
//  -----------------------------------------------------
const Wrapper = styled.div `height: 100%; width: 100%; display: flex; flex-direction: column; background: rgba(0,0,0,.4);`
    const Header  = styled.h2  `padding: 4px 0 8px 0; text-align: center; font-size: 180%; color: #fff; `
// -- About music text
    const TextWR    = styled.div `width: 80%; margin: 10px auto; display: inline-flex; flex-direction: column; justify-content: center; 
    padding: 30px 20px; text-align: left; border-radius: 50px; border: 2px dashed #620040; background: #27004e;
    box-shadow: inset 0 0 20px 0 #000, 0 0 50px 20px #630e41;`
    const Paragraph = styled.p   `padding: 20px 10px; margin: 5px 0; box-shadow: inset 0 0 20px 0 #000, 0 0 100px 5px #001d62;
    font-size: 140%; color: #fff; border-bottom: 2px ridge #333; border-top: 2px ridge #333; border-radius: 20px;`
    
    export default Music